import React, { useState } from "react";
import { useFormik } from "formik";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import { Password } from "primereact/password";
import { Divider } from "primereact/divider";
import { classNames } from "primereact/utils";
import { login } from "services/authService/";
import { Message } from "primereact/message";
import { TOKEN_KEY } from "utils/constant";
import { Navigate } from "react-router";

import "assets/css/login.css";

export const Login = (props) => {
  const [logginData, setLogginData] = useState({ loading: false, error: null });

  const formik = useFormik({
    initialValues: {
      username: "",
      password: "",
    },
    validate: (data) => {
      let errors = {};

      if (!data.username) {
        errors.username = "Username is required.";
      }

      if (!data.password) {
        errors.password = "Password is required.";
      }

      return errors;
    },
    onSubmit: (data) => {
      setLogginData({ ...logginData, loading: true });
      login({ username: data.username, password: data.password }).then(
        (response) => {
          console.log("login", response);
          localStorage.setItem(TOKEN_KEY, response.data.authorization);
          setLogginData({ loading: false, error: null });
          //  props.history.push("/");
        },
        (error) => {
          setLogginData({ loading: false, error: error.message });
        }
      );
      formik.resetForm();
    },
  });

  const isFormFieldValid = (name) =>
    !!(formik.touched[name] && formik.errors[name]);
  const getFormErrorMessage = (name) => {
    return (
      isFormFieldValid(name) && (
        <small className="p-error">{formik.errors[name]}</small>
      )
    );
  };

  const passwordHeader = <h6>Pick a password</h6>;
  const passwordFooter = (
    <React.Fragment>
      <Divider />
      <p className="p-mt-2">Suggestions</p>
      <ul className="p-pl-2 p-ml-2 p-mt-0" style={{ lineHeight: "1.5" }}>
        <li>At least one lowercase</li>
        <li>At least one uppercase</li>
        <li>At least one numeric</li>
        <li>Minimum 8 characters</li>
      </ul>
    </React.Fragment>
  );

  return (
    <div className="form-demo">
      <div className="p-d-flex p-jc-center">
        <div className="card">
          <form onSubmit={formik.handleSubmit} className="p-fluid">
            <div className="p-field">
              <span className="p-float-label">
                <InputText
                  id="username"
                  name="username"
                  value={formik.values.username}
                  onChange={formik.handleChange}
                  autoFocus
                  className={classNames({
                    "p-invalid": isFormFieldValid("username"),
                  })}
                />
                <label
                  htmlFor="username"
                  className={classNames({
                    "p-error": isFormFieldValid("username"),
                  })}
                >
                  Username*
                </label>
              </span>
              {getFormErrorMessage("username")}
            </div>

            <div className="p-field">
              <span className="p-float-label">
                <Password
                  id="password"
                  name="password"
                  value={formik.values.password}
                  onChange={formik.handleChange}
                  toggleMask
                  className={classNames({
                    "p-invalid": isFormFieldValid("password"),
                  })}
                  header={passwordHeader}
                  footer={passwordFooter}
                />
                <label
                  htmlFor="password"
                  className={classNames({
                    "p-error": isFormFieldValid("password"),
                  })}
                >
                  Password*
                </label>
              </span>
              {getFormErrorMessage("password")}
            </div>

            <Button
              type="submit"
              label="Submit"
              className="p-mt-2"
              loading={logginData.loading}
            />
            {logginData.error && (
              <div className="p-mt-4">
                <Message severity="error" text={logginData.error} />
              </div>
            )}
          </form>
        </div>
      </div>
    </div>
  );
};
